* {
    margin: 0;
    padding: 0;
    font-family: "Roboto", sans-serif;
}

.App {
    height: 600px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.game {
    gap: 40px;
    display: flex;
    justify-content: center;
}

.status-screen {
    font-size: 30px;
}

.square {
    background: #d6d6d6;
    border: solid;
    border-width: 1px;
    border-color: #01233d;
    height: 100px;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 60px;
}

.square.clicked {
    cursor: not-allowed;
}

.board {
    display: grid;
    grid-template-columns: repeat(3, auto);
    align-items: center;
    justify-content: center;
    grid-gap: 5px;
}
  
.player-turn {
    color: #01233d;
    font-size: 2rem;
    margin: 2rem 0;
}